.scroll_down{
    position: absolute;
    top: 250px;
    left: 45%;
    z-index: 10;
    -webkit-animation: scroll_down 5s  infinite; /* Chrome, Safari, Opera */ 
    animation: scroll_down 1s linear infinite;
}

@keyframes scroll_down {
    0%   {transform:translate(0px,0px);opacity: 0}
    50%  {transform:translate(0px,8px);opacity: 0.4}
    100%   {transform:translate(0px,16px);opacity:0.8}
    /*75%  {top: 50px}
    100% {top: 100px;}*/
}

